import React, {
  useCallback,
  useMemo,
  useState
} from 'react';
import {
  Action,
  State,
} from './Scenario';
import Typewriter from './Typewriter';
import './Scene.scss';

// TODO: delayTypewriter is a crutch because it's difficult to get the exact animation delay at this point without a full rewrite of the Typewriter component
function Scene({ state, scale, delayTypewriter, onAction }: { state: State, scale: number, delayTypewriter: boolean, onAction: (action: Action) => any }) {
  const delay = useMemo(() => (delayTypewriter ? 2.5 : 0), []);
  const [ actionsAnimationState, setActionsAnimationState ] = useState('paused');
  
  const onAnimationEnd = useCallback(() => {
    setActionsAnimationState('running');
  }, []);

  const displayDefaultAction = state.actions.length === 1 && state.actions[0].description === "";

  return <div className="Scene" style={{ transform: `scale(${scale})`}}>
    <img className="background-image" src={state.background} alt={state.description} />
    {state.characterImage && <div className="character-image"><img src={state.characterImage} alt={state.characterName || "Character"} /></div>}
    <div className="description">
      {state.characterName && <div className="character-name">{state.characterName}</div>}
      <p contentEditable>{state.description}</p>
    </div>
    {displayDefaultAction
      ? <a className={`default-action animation-${actionsAnimationState}` }href={`#${state.actions[0].targetStateName}`} onClick={event => { onAction && onAction(state.actions[0]); event.preventDefault(); }}>{"Continue"}</a>
      : (
        state.actions.length > 0 &&
          <ul className={`actions animation-${actionsAnimationState}`}>
            {state.actions.map(action => 
              <li key={action.name}><a contentEditable>{action.description}</a></li>
            )}
          </ul>
      )
    }
  </div>
}

export default Scene;
