import React, {
  useCallback,
  useEffect,
  useRef,
  useState
} from 'react';
import { SwitchTransition, CSSTransition } from 'react-transition-group';
import debounce from 'lodash.debounce';
import Scenario, {
  Action,
  DEFAULT_STATE,
  State,
} from './Scenario';
import Scene from './Scene';
import './App.css';

const CIRCLE_ANIMATION_NAME = "circle";
const STATIC_ANIMATION_NAME = "static";

function App() {
  const [ state, setState ] = useState<State>(DEFAULT_STATE);
  const [ scale, setScale ] = useState<number>(1);
  const [ transition, setTransition ] = useState<string>(CIRCLE_ANIMATION_NAME);
  const [ scenario, setScenario ] = useState<Scenario | null>(null);

  useEffect(() => {
    fetch('2022-01-25-contenteditable.xml')
      .then(value => value.text())
      .then(text => {
        const parser = new DOMParser();
        return parser.parseFromString(text, "application/xml");
      })
      .then(doc => {
        const scenario = Scenario.fromXMLDocument(doc);
        setScenario(scenario);
        setState(Object.assign({}, scenario.currentState));
      })
  }, []);

  const onAction = useCallback((action: Action) => {
    if (scenario) {
      scenario.activate(action.targetStateName);
      setState(previousState => {
        if (previousState.background === scenario.currentState.background) {
          setTransition(STATIC_ANIMATION_NAME);
        } else {
          setTransition(CIRCLE_ANIMATION_NAME);
        }
        // Defer actually setting the new state until the next frame, such that the transition will be switched out first, and the scene after.
        setTimeout(() => setState(Object.assign({}, scenario.currentState)), 0);
        return previousState;
      });
    }
  }, [ scenario ]);

  const appRef = useRef<HTMLDivElement>(null);

  // NOTE: Current Scene design relies on the 1280×720 resolution, so instead of rewriting all of that for mobile and various aspect ratios, we're going to scale the whole thing.
  useEffect(() => {
    const resizeListener = debounce(
      () => {
        if (appRef.current) {
          setScale(Math.min(
            appRef.current.clientWidth / 1280,
            appRef.current.clientHeight / 720
          ))
        }
      },
      333
    );

    resizeListener();
    window.addEventListener('resize', resizeListener);
    return () => window.removeEventListener('resize', resizeListener);
  }, []);

  return (
    <div className="App" ref={appRef}>
      {scenario && <SwitchTransition>
        <CSSTransition key={state.name} classNames={transition} addEndListener={(node, done) => {
              node.addEventListener("animationend", done, false);
            }}>
          <Scene scale={scale} delayTypewriter={transition !== STATIC_ANIMATION_NAME} state={state} onAction={onAction} />
        </CSSTransition>
      </SwitchTransition>}
    </div>
  );
}

export default App;
