
interface ScenarioData {
  [key: string]: State;
}

export interface State {
  name: string;
  background: string;
  description: string;
  characterImage?: string;
  characterName?: string;
  actions: Action[];
}

export interface Action {
  name: string;
  description: string;
  targetStateName: string;
}

export const DEFAULT_STATE: State = {
  name: "default",
  background: "loading.gif",
  description: "Please wait while the scenario is loading.",
  actions: [],
}

class Scenario {
  static fromXMLDocument(doc: XMLDocument) {
    const scenario = new Scenario();
    const allCells = Array.from(doc.querySelectorAll('mxCell'));
    let firstStateAssigned = false;

    allCells
      .filter(mxCell => mxCell.getAttribute('parent') === '1' && mxCell.getAttribute('edge') === null)
      .forEach(mxCell => {
        const state: State = {
          name: mxCell.getAttribute('id') || '',
          description: mxCell.getAttribute('value') || '',
          background: '',
          actions: [],
        }

        // Collect all contained images
        const images = allCells
          .filter(mxCell => mxCell.getAttribute('parent') === state.name)
          .map(mxCell => {
            const image: {src?:string,text?:string} = {
              text: mxCell.getAttribute('value') || undefined
            };
            const style = mxCell.getAttribute('style');
            if (style?.startsWith('shape=image;')) {
              const imageMatch = style.match(/image=([^;]+);/);
              if (imageMatch) {
                image.src = imageMatch[1];
                if (image.src.startsWith('data:image/png,')) {
                  image.src = image.src.replace('data:image/png,', 'data:image/png;base64,');
                }
              }
            }
            return image;
          })

        // Of all the images:
        // * the first one (furthest back) is the background.
        // * the second one is a character, and may have a name
        if (images[0]) {
          state.background = images[0].src || '';
        }
        if (images[1]) {
          state.characterImage = images[1].src;
          state.characterName = images[1].text;
        }

        // Find actions
        allCells
          .filter(mxCell => mxCell.getAttribute('source') === state.name)
          .forEach(mxCell => {
            const action: Action = {
              name: mxCell.getAttribute('id') || '',
              description: mxCell.getAttribute('value') || '',
              targetStateName: mxCell.getAttribute('target') || '',
            }

            state.actions.push(action)
          })

        scenario.data[state.name] = state;

        if (!firstStateAssigned) {
          // NOTE: State computation was meant to be evolutive, but that idea needs to be thought through.
          scenario.currentState = state;
          firstStateAssigned = true;
        }
      })

    return scenario;
  }

  constructor(
    public data: ScenarioData = {},
    public currentState: State = Object.assign({}, DEFAULT_STATE),
  ) {}

  activate(targetStateName: string) {
    if (this.data[targetStateName]) {
      // NOTE: State computation was meant to be evolutive, but that idea needs to be thought through.
      this.currentState = this.data[targetStateName];
    }
    // TODO: Throw?
  }
}

export default Scenario;
